// class 

// --------------------------------------------------------------------

class Header extends PureComponent {


    getListArea = () => {
        if (this.props.focused) {
            console.log("show", this.props);
            return (

                <SearchInfoList >

                    <SearchInfoTitle>
                        
                        <SearchInfoSwitch>
                            <BsGem size={12} />  
                        </SearchInfoSwitch>
                    </SearchInfoTitle>
                    <div>
                        <SearchDiv>
                            {
                                this.props.list.map((listObj) => {
                                    return <SearchInfoItems key={listObj}>{listObj}</SearchInfoItems>

                                })
                            }
                        </SearchDiv>
                    </div>
                </SearchInfoList>
            )
        } else {
            return null;
        }
    }

    render() {
        console.log(this.props);
        console.log("header_login_props", this.props.login)

        return (
            <div>
                <HeaderWrapper>
                    <Link to='/'>
                        <Logo />
                    </Link>

                    <Nav>
                        <NavItem className='left active'>
                            <Link to='/'>
                                
                            </Link>
                        </NavItem>
                        <NavItem className='left '> APP</NavItem>
                        {
                            this.props.login ?
                                <NavItem onClick={this.props.loginOut} className='right'></NavItem> :
                                <Link to='/login'> <NavItem className='right active'> </NavItem></Link>
                        }


                        <NavItem className='right'> Aa</NavItem>

                        {/*   */}
                        <SearchWrapper>

                            <CSSTransition
                                in={this.props.focused}
                                timeout={200}
                                classNames='slide'>
                                <NavSearch className={this.props.focused ? 'focused' : ' '}
                                    onFocus={this.props.handleInputFocus}
                                    onBlur={this.props.handleInputBlur}>
                                </NavSearch>
                            </CSSTransition>

                            <BarSearchWrapper className={this.props.focused ? 'focused' : ' '}>
                                <BsSearch size={20} />

                                {/* list */}
                                {this.getListArea()}

                            </BarSearchWrapper>
                        </SearchWrapper>

                    </Nav>
                    <Addition>
                        <Link to='/write'>
                            <Button className='writting'>
                                <RightSpanWrapper>
                                    <BsPencilSquare size={18} />
                                </RightSpanWrapper> 
                            </Button>
                        </Link>
                        <Button className='reg'></Button>

                    </Addition>
                </HeaderWrapper >
            </div>
        )
    }

}




//  pagetotalpage  ===

const mapStateToProps = (state) => {
    return {
        // statefocusfocused  header
        focused: state.header.focused,
        list: state.header.homeList,
        page: state.header.page,
        totalPage: state.header.totalPage,
        mouseIn: state.header.mouseIn,

        //   state header  login login 
        login: state.login.login
    }
}

