const mapDispatchToProps = (dispatch) => {
    return {
        // action  createFocusedAction() createFocusedAction
        // dispatch action returntype data reduce
        handleInputFocus: () => {
            dispatch(GetListAction());
            dispatch(createFocusedAction());

        },
        handleInputBlur: () => dispatch(createBlurAction()),

        //  loginout action  login actionlogin reduce 
        //  header  loginstate  loginactionreducer
        loginOut: () => dispatch(createLoginOut())
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(Header)